@media all and (max-width: 767px) {
  .main-nav {
    background-color: #ffffff;
    position: relative;
  }

  .main-nav__list {
    list-style-type: none;
    margin: 0;
    padding: 0;
  }

  .main-nav__list .main-navigation__list {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    transition: 0.5s ease-in-out;
  }

  .main-nav__toggle {
    display: none;
    position: absolute;
    min-height: 23px;
    width: 24px;
    top: -46px;
    right: 0;
    border: none;
    border-bottom: 2px solid #000000;
    background-color: transparent;
    cursor: pointer;
    transition: 0.3s ease-in-out;
  }
  .main-nav__toggle::before {
    position: absolute;
    content: "";
    top: 12px;
    left: 0;
    display: block;
    width: 100%;
    height: 2px;
    background-color: #000000;
    transition: 0.3s ease-in-out;
  }
  .main-nav__toggle::after {
    position: absolute;
    bottom: 17px;
    left: 0;
    display: block;
    width: 100%;
    height: 2px;
    background-color: #000000;
    content: "";
    transition: 0.3s ease-in-out;
  }

  .main-nav--closed .main-nav__toggle {
    display: block;
  }

  .main-nav--closed .main-nav__list {
    display: none;
  }

  .main-nav--opened .main-nav__list {
    position: absolute;
    top: -2px;
    left: -20px;
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 320px;
    background-color: #ffffff;
    z-index: 3;
    opacity: 1;
  }

  .main-nav--opened .main-nav__toggle {
    display: block;
    min-height: 46px;
    min-width: 30px;
    top: -60px;
    border: 0;
  }
  .main-nav--opened .main-nav__toggle::before {
    visibility: visible;
    transform: rotate(45deg);
    top: 23px;
  }
  .main-nav--opened .main-nav__toggle::after {
    visibility: visible;
    transform: rotate(-43deg);
    top: 22px;
  }
  .main-nav--opened .main-nav__toggle .main-nav--nojs .main-nav__toggle {
    display: none;
  }
}
@media (min-width: 768px) {
  .main-nav__list {
    padding: 0;
    margin: 0;
  }

  .main-nav--closed .main-nav__toggle {
    display: none;
  }

  .main-nav--opened .main-nav__toggle {
    display: none;
  }
}
.site-navigation {
