}
.site-navigation__item:first-child::before {
  position: absolute;
  top: -2px;
  right: 0;
  left: 0;
  content: "";
  height: 1px;
  width: 320px;
  background-color: #e6e6e6;
}
.site-navigation__item::after {
  position: absolute;
  bottom: 0;
  right: 0;
  left: 0;
  content: "";
