.options__custom-checkbox::before {
  display: none;
  position: absolute;
  top: 15%;
  right: 17%;
  content: "";
  width: 17px;
  height: 11px;
  border-left: 2px solid #68b738;
  border-bottom: 2px solid #68b738;
  transform: rotate(-52deg);
}

.options__checkbox:checked + .options__custom-checkbox::before {
  display: block;
}

.options__checkbox:hover + .options__custom-checkbox {
  border-color: #bababa;
}

.options__checkbox:disabled + .options__custom-checkbox,
.options__checkbox:disabled ~ .options__text {
  opacity: 0.3;
}

